/* Chat.css */

.chat-container {
    display: flex;
    flex-direction: column;
    height: 100vh;
    padding: 20px;
    background-color: #f0f0f0;
  }
  
  .chat {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    overflow-y: auto;
    padding: 10px;
  }
  
  .message {
    max-width: 70%;
    margin: 5px;
    padding: 10px;
    border-radius: 8px;
  }
  
  .message-content {
    word-wrap: break-word;
  }
  
  /* Style left-aligned messages */
  .message.left {
    align-self: flex-start;
    background-color: #e1ffc7;
  }
  
  /* Style right-aligned messages */
  .message.right {
    align-self: flex-end;
    background-color: #c7e0ff;
  }
  
  /* Style the input field and send button (add these elements in the component) */
  .input-container {
    display: flex;
    align-items: center;
    margin-top: 10px;
  }
  
  input[type="text"] {
    flex-grow: 1;
    padding: 10px;
    /* border: 1px solid #ccc; */
    border-radius: 4px;
    outline: none;
  }
  
  button {
    padding: 10px 20px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    outline: none;
  }
  
  button:hover {
    background-color: #0056b3;
  }
  